.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1{
  text-align: center;
  font-family: 'Abril Fatface', cursive;
  font-size: 5em;
}

h2{
  text-align: center;
  font-family: 'Acme', sans-serif;
  color: #64debd;
  font-size: 3em;
}

h3, p{
  text-align: center;
  color: whitesmoke;
}

h3{
  font-size: 2em;
}

p{
  margin: 0 5em 0 5em;
  font-size: 21px;
}

a{
  font-size: 32px;
}